import React from "react";
export default function MiniHeader(props){
    let color= props.content.primaryColor
    return(
        <React.Fragment>
            <div style={{
                margin: "auto",
                width: "90vw",
                textAlign: "center",
                padding: "20px"
            }}>
                <h2 style={{color,fontSize:"27px"}}>{props.content.title}</h2>
                <p style= {{color:"#0000008c"}}>{props.content.discritpion}</p>

            <div className="breaker" style={{
                height: "4px",
                width: "20%",
                backgroundImage: "linear-gradient(to left, #f4eb82, transparent)",
                borderRadius: "1vw",
                float: "left",
                marginLeft: "40%",
                marginBottom: "6vh",
            }}></div>
            </div>
        </React.Fragment>
    )
}